import {
  ShellBar,
  ThemeProvider,
  Input,
  Avatar,
  SideNavigation,
  SideNavigationItem,
  SideNavigationSubItem,
  Icon,
} from "@ui5/webcomponents-react";
// eslint-disable-next-line no-unused-vars
import AllIcons from "@ui5/webcomponents-icons/dist/AllIcons.js";
import React, { useState } from "react";
import { ethers } from "ethers";
import "./App.css";
import { Route, HashRouter } from "react-router-dom";
import Asset from "./pages/Asset";
import QR from "./pages/QR";
import Error from "./pages/Error";
import Account from "./pages/Account";
import abi from "./abi.json";
import { isMobile } from "react-device-detect";
import WalletConnectProvider from "@walletconnect/web3-provider";

let provider = null;
let signer = null;
let address = null;
let roProvidernoUser = new ethers.providers.JsonRpcProvider(
  "https://polygon-mainnet.infura.io/v3/d8e7160dc78d48dba6ca40b11fdf1cd5" ||
    "https://rpc-mainnet.matic.network"
);
let magentaDAO = new ethers.Contract(
  "0xe939789E151608D7442E2e6Ff4ea4E2eaf314cF3",
  abi,
  roProvidernoUser
);
const roProvider = new WalletConnectProvider({
  rpc: {
    137: "https://polygon-mainnet.infura.io/v3/d8e7160dc78d48dba6ca40b11fdf1cd5",
  },
});

export async function ethersConnect() {
  try {
    if (isMobile) {
      await roProvider.enable();
      provider = new ethers.providers.Web3Provider(roProvider);
    } else {
      provider = new ethers.providers.Web3Provider(window.ethereum, "any");
      await provider.send("eth_requestAccounts", []);
    }

    //[provider.getBlockNumber().then((b) => b - 10000),provider.getBlockNumber().then((b) => b )]

    signer = provider.getSigner();
    address = await signer.getAddress();
    magentaDAO = new ethers.Contract(
      "0xe939789E151608D7442E2e6Ff4ea4E2eaf314cF3",
      abi,
      signer
    );

    //  let filter = magentaDAO.filters.TransferSingle();
    // console.log(filter)
    // console.log(await magentaDAO.filter.TransferSingle());
    // console.log(await magentaDAO.queryFilter(filter, 17862525 , 17862925));

    return await signer.getAddress();
  } catch (error) {
    //alert("Please install Metamask!")
    console.log("e :" + error);

    return "Read only";
  }
}

export async function getTokenId(ipfsId) {
  try {
    ipfsId = ipfsId.split("/")[0];
    const rslt = magentaDAO.getTokenId(ipfsId).then((result) => {
      return result.toNumber();
    });
    return rslt;
  } catch {
    return "";
  }
}

function handleSearch(value) {
  window.location.assign("/#/asset/" + value);
}

function handleAccount(value) {
  window.location.assign("/#/account/" + address);
}

function handleQR() {
  window.location.assign("/#/qr");
}

function handleHome() {
  window.location.assign("/#/");
}

function App() {
  const [value, setValue] = useState("");
  const [userAddress, setuserAddress] = useState("Connect Wallet");
  const [menu, setMenu] = useState("none");
  const [profile, setProfile] = useState(false);

  return (
    <ThemeProvider>
      <HashRouter>
        <ShellBar
          logo={
            <img
              alt="MagentaDAO"
              mdxtype="img"
              originaltype="img"
              src="../MagentaDAO.png"
            />
          }
          onLogoClick={() => handleHome()}
          onMenuItemClick={function noRefCheck() {}}
          style={{ overflow: "scroll" }}
          className="Shell"
          onProfileClick={() => setMenu("block")}
          profile={
            <Avatar
              icon="menu"
              colorScheme="Accent10"
              size="XS"
              shape="Square"
            ></Avatar>
          }
        ></ShellBar>
        <br></br>
        <br></br>
        <div className="Menu" style={{ display: menu }}>
          <SideNavigation
            className=""
            fixedItems={
              <>
                <SideNavigationItem icon="information" text="Impressum" />
              </>
            }
            onSelectionChange={function noRefCheck() {}}
            slot=""
            style={{}}
            tooltip=""
          >
            <SideNavigationItem
              icon="nav-back"
              text="Back"
              onClick={() => setMenu("none")}
            />
            <SideNavigationItem
              className=""
              icon="group"
              onSelectionChange={function noRefCheck() {}}
              onClick={() =>
                ethersConnect().then((a) => {
                  setuserAddress(a.substring(0, 6) + "..." + a.slice(-4));
                  setProfile(!profile);
                })
              }
              onChange={(e) => setuserAddress(e.target.userAddress)}
              text={userAddress}
              tooltip={userAddress}
            >
              <SideNavigationSubItem
                text="Account"
                icon="customer"
                onClick={() => {
                  handleAccount();
                  setMenu("none");
                }}
              />
            </SideNavigationItem>
          </SideNavigation>
        </div>
        <Route exact strict path="/">
          <div className="App">
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <div className="Scan">
              <img
                alt="click"
                mdxtype="img"
                originaltype="img"
                src="../button.png"
                width="200px"
                height="200px"
                onClick={() => handleQR()}
              />
            </div>
            <br />
            <br />
            <br />
            <div className="Search">
              <Input
                onChange={(e) => setValue(e.target.value)}
                style={{}}
                type="Text"
                icon={
                  <Icon name="search" onClick={() => handleSearch(value)} />
                }
              />
            </div>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
            <br></br>
          </div>
        </Route>

        <Route path="/asset">
          <Asset />
        </Route>
        <Route path="/qr">
          <QR />
        </Route>
        <Route path="/error">
          <Error />
        </Route>
        <Route path="/account">
          <Account />
        </Route>
      </HashRouter>
    </ThemeProvider>
  );
}

export default App;
